/*
 * MainSchedule CSS Module
 */

.main_container{
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 100%;
}

.list_container{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    width: 70%;
    height: 80vh;
}

.filter_container{
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
    height: 80vh;
    width: 30%;
}

